<template>
  <div class="relative slider-wrapper w-full flex flex-col">

    <div class="absolute w-full h-full" v-for="(slide,index) in slider" :key="index">
      <transition name="fade">
      <img class="object-cover w-full h-full" @touchend="myTouchStartHandler()" v-if="index === slideNum" :src="slider[slideNum]" alt="">
      </transition>
    </div>

    <div class="w-full absolute bottom-2 flex justify-center items-center z-10">
      <div class="w-3 h-3 mx-1 rounded-full cursor-pointer"
      :class="paginationColor(index)" 
      @click="changeSlide(index)" 
      v-for="(slide,index) in slider"
      :key="slide"></div>
    </div>

  </div>
</template>

<script>
export default {
  data(){
    return{
      slideNum: 0,
      intervalTime: 0,
      slider: [require("../assets/slide1.jpg"), require("../assets/slide2.jpg"), require("../assets/slide3.jpg")]
    }
  },
  mounted(){
    this.setTime()
  },
  methods:{
    changeSlide(index){
      return this.slideNum = index, clearInterval(this.intervalTimer), this.setTime();
    },
    paginationColor(index){
      return this.slideNum == index ? "bg-black" : "bg-gray-500";
    },
    setTime(){
      this.intervalTimer = setInterval(() => { return this.slideNum == 2 ? this.slideNum = 0 : this.slideNum++ }, 3000)
    }
  }
}
</script>

<style>
.slider-wrapper{
  overflow: hidden;
  height: 500px;
}
.fade-enter-active, .fade-leave-active {
  transition: 1s all ease-out;
}
.fade-enter-from{
  transform: translateX(100%);
}
.fade-leave-to{
  transform: translateX(-100%);
}
</style>